.secondary-navigation a[href*="vimeo.com"]:after,
.secondary-navigation a[href*="wordpress.com"]:after,
.secondary-navigation a[href*="wordpress.org"]:after,
.secondary-navigation a[href*="youtube.com"]:after,
.secondary-navigation a[href*="vk.com"]:after,
.secondary-navigation a[href*="twitch.tv"]:after {
    font-family: "Font Awesome 5 Brands";
    font-weight: 400; }

.secondary-navigation a[href*="mailto:"]:after,
.secondary-navigation a[href$="/feed/"]:after { font-family: "Font Awesome 5 Free"; font-weight: 900; }

.secondary-navigation a[href$="/feed/"]:after {content: "\f09e"; color: #ff9900; }
